import { useState } from 'react'
import Link from 'next/link'
import Image from 'next/image'

type ProjectCategory = 'fullstack' | 'data'

interface Project {
  name: string
  description: string
  image: string
  link: string
  category: ProjectCategory
}

const projects: Project[] = [
  {
    name: "Project Name",
    description: "I created this personal project in order to show how to create an interface in Figma using a portfolio as an example.",
    image: "/project1.jpg",
    link: "/projects/1",
    category: 'fullstack'
  },
  {
    name: "Project Name",
    description: "What was your role, your deliverables, if the project was personal, freelancing.",
    image: "/project2.jpg",
    link: "/projects/2",
    category: 'data'
  },
  {
    name: "Project Name",
    description: "You can also add in this description the type of the project, if it was for web, mobile, data analysis",
    image: "/project3.jpg",
    link: "/projects/3",
    category: 'fullstack'
  },
]

export default function Projects() {
  const [activeCategory, setActiveCategory] = useState<ProjectCategory>('fullstack')

  const filteredProjects = projects.filter(project => project.category === activeCategory)

  return (
    <section className="py-20 px-4 lg:px-8">
      <div className="max-w-6xl mx-auto">
        <h2 className="text-3xl font-bold text-center mb-4">Projects</h2>
        
        {/* Category Tabs */}
        <div className="flex justify-center space-x-8 mb-12">
          <button
            onClick={() => setActiveCategory('fullstack')}
            className={`text-lg font-medium pb-2 border-b-2 transition-colors ${
              activeCategory === 'fullstack' 
                ? 'border-aqua text-aqua' 
                : 'border-transparent hover:text-aqua'
            }`}
          >
            Full-stack
          </button>
          <button
            onClick={() => setActiveCategory('data')}
            className={`text-lg font-medium pb-2 border-b-2 transition-colors ${
              activeCategory === 'data' 
                ? 'border-aqua text-aqua' 
                : 'border-transparent hover:text-aqua'
            }`}
          >
            Data
          </button>
        </div>

        {/* Projects Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
          {filteredProjects.map((project, index) => (
            <div key={index} className="bg-white rounded-lg overflow-hidden shadow-lg">
              <div className="grid grid-cols-1 lg:grid-cols-2 gap-4">
                <div className="p-6">
                  <h3 className="text-xl font-bold mb-2">{project.name}</h3>
                  <p className="text-gray-600 mb-4">{project.description}</p>
                  <Link 
                    href={project.link}
                    className="inline-block px-4 py-2 border border-aqua text-aqua rounded hover:bg-aqua hover:text-white transition-colors"
                  >
                    View Project
                  </Link>
                </div>
                <div className="relative h-48 lg:h-auto">
                  <Image
                    src={project.image}
                    alt={project.name}
                    fill
                    className="object-cover"
                  />
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  )
} 